import { Table } from 'antd'
import React, { useMemo } from 'react'
import plural from 'plural-ru'

import { calcRank, calcSolveCustomerClaimsPercentage, rankToColor } from '../../helpers/rank'
import ColoredText from '../../components/ColoredText'

import RankBlockTitle from './RankBlockTitle'

import styles from './styles'

const ServiceStat = ({
  website,
                  fakeReviews,
                  feedbackWithClientsDirection,
                       solveCustomerClaimsPercentage,
                  forumReviewsDirection,
                  sideServicesRank,
                  incomplete,
                  hideRank,
                  titleFontSize,
                  valueFontSize,
  rowColor,
                  ...rest
                }) => {
  const rank = useMemo(() => calcRank({
    website,
    fakeReviews,
    feedbackWithClientsDirection,
    forumReviewsDirection,
    solveCustomerClaimsPercentage,
    sideServicesRank,
  }), [fakeReviews, feedbackWithClientsDirection, forumReviewsDirection, sideServicesRank]);
  const rankData = [
    ...(
      !hideRank ? [{
        key: 'rank',
        title: <RankBlockTitle style={{ fontSize: titleFontSize }} title="  " strong description="              ,   ,     " />,
        value: (
          <span style={{ color: !incomplete && rank !== null ? rankToColor(rank) : 'gray', fontSize: valueFontSize }} css={styles.ratingValue}>
            <b>{rank !== null ? rank : ' '}</b>
          </span>
        ),
        color: 'yellow',
      }] : []
    ),
    {
      key: 'feedbackWithClientsDirection',
      title: <RankBlockTitle style={{ fontSize: titleFontSize }} title="  " description="          .      " />,
      value: <span css={styles.ratingValue} style={{ fontSize: valueFontSize }}>
        {solveCustomerClaimsPercentage > 0 ? <ColoredText type="safe" strong>{calcSolveCustomerClaimsPercentage({ percentage: solveCustomerClaimsPercentage})}%</ColoredText> : null}
        {solveCustomerClaimsPercentage === -1 ? <ColoredText disabled strong> </ColoredText> : null}
        {solveCustomerClaimsPercentage === 0 ? <ColoredText type="danger" strong></ColoredText> : null}
      </span>,
      color: rowColor,
    },
    // {
    //   key: 'forumReviewsDirection',
    //   title: <RankBlockTitle title="    " description="         ,   toyota-club.com.ua, drive2.ru  ." />,
    //   value: <span css={styles.ratingValue}>
    //     {forumReviewsDirection === 2 && <ColoredText type="safe" strong></ColoredText>}
    //     {forumReviewsDirection === 1 && <ColoredText type="warning" strong></ColoredText>}
    //     {forumReviewsDirection === 0 && <ColoredText type="danger" strong></ColoredText>}
    //     {forumReviewsDirection === -1 && <ColoredText disabled strong></ColoredText>}
    //   </span>,
    // },
    {
      key: 'fakeReviews',
      title: <RankBlockTitle style={{ fontSize: titleFontSize }} title="  " description="       -      ,   ,         ." />,
      value: <span css={styles.ratingValue} style={{ fontSize: valueFontSize }}>
        {
          fakeReviews ? (
            <ColoredText type="danger" strong></ColoredText>
          ) : (
            <ColoredText type="safe" strong></ColoredText>
          )
        }
      </span>,
      color: rowColor,
    },
    ...sideServicesRank.map(o => ({
      key: JSON.stringify(o),
      title: <RankBlockTitle style={{ fontSize: titleFontSize }} title={<a href={o.link} target="_blank"> <span>{o.name}</span></a>} />,
      value: (
        <span css={styles.ratingValue} style={{ fontSize: valueFontSize }}>
          <b style={{ color: rankToColor(o.rank) }}>
            {
              o.rank !== null ? <>
                <span css={styles.rankValue}>
                  {parseFloat(o.rank).toFixed(1)}</span> <span css={styles.reviewsAmount}><a href={o.link} target="_blank">({o.reviewsAmount} {plural(o.reviewsAmount, '', '', '')})</a>
                </span>
              </> : ' '
            }
          </b>
        </span>
      ),
      color: rowColor,
    }))
  ];

  const rankColumns = useMemo(() => [
    {
      title: 'Title',
      dataIndex: 'title',
      key: 'title',
      render(text, record) {
        return {
          props: {
            style: {
              background: record.color,
              paddingTop: 10,
              paddingBottom: 10,
            },
          },
          children: <div>{text}</div>,
        };
      },
    },
    {
      title: 'Value',
      dataIndex: 'value',
      key: 'value',
      render(text, record) {
        return {
          props: {
            style: {
              background: record.color,
              paddingTop: 10,
              paddingBottom: 10,
            },
          },
          children: <div>{text}</div>,
        };
      },
    }
  ], []);

  return <Table
    {...rest}
    dataSource={rankData}
    columns={rankColumns}
    showHeader={false}
    pagination={false}
  />;
}

export default React.memo(ServiceStat);
