const DAY_TO_LABEL_MAP = {
  'monday': '',
  'tuesday': '',
  'wednesday': '',
  'thursday': '',
  'friday': '',
  'saturday': '',
  'sunday': '',
}

export const mapDayToLabel = (day) => {
  const days = day.split('To');
  return days.map(day => DAY_TO_LABEL_MAP[day.toLowerCase()]).join(' - ');
};
