import React, { useCallback, useRef, useState } from 'react'
import { Modal, Input } from 'antd';

export default React.memo(({ selectedServiceName, onCancel }) => {
  const [status, setStatus] = useState('');
  const formRef = useRef();


  const submitForm = useCallback(() => {
    const form = formRef.current;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        setStatus('SUCCESS');
      } else {
        setStatus('ERROR');
      }
      onCancel();
    };
    xhr.send(data);
  }, []);

  return (
    <Modal
      title={`   ${selectedServiceName}`}
      visible={!!selectedServiceName}
      onOk={submitForm}
      onCancel={onCancel}
      okText=""
      cancelText=""
      zIndex={1111111111}
    >
      <form
        ref={formRef}
        onSubmit={submitForm}
        action="https://formspree.io/xqkbnpbw"
        method="POST"
      >
        <div>
          <i>
                  ,      
          <br />
          <br />
             ,    ,        
          </i>
          <br /><br />
        </div>
        <input name="service" value={selectedServiceName} hidden />
        <label> :</label>
        <Input name="phone" />
        <br />
        <br />
        <label> ( ):</label>
        <Input.TextArea type="text" name="message" />
        {status === "SUCCESS" ? <p>Thanks!</p> : null}
        {status === "ERROR" ? <p>Ooops! There was an error.</p> : null}
      </form>
    </Modal>
  );
})
